.App {
  font-family: sans-serif;
  text-align: center;
}

:root {
  --bg-color: hsl(49 37% 94%);
  --typewriter-speed: 3s;
  --typewriter-characters: 23;
}

body {
  margin: 0;
  font-family: "Source Code Pro", sans-serif;
  min-height: 100vh;
  display: grid;
  place-content: center;
  text-align: center;
  background-color: var(--bg-color);
}
h1 {
  font-size: clamp(1rem, 3vw + 1rem, 4rem);
  position: relative;
  font-family: "Source Code Pro", monospace;
  position: relative;
  font-weight: 200;
}

p {
  font-size: 12px;
  font-family: "Source Code Pro", monospace;
  font-weight: 200;
}
h1::before,
h1::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}

h1::before {
  background: var(--bg-color);
  animation: typewriter var(--typewriter-speed) 1s
    steps(var(--typewriter-characters)) forwards;
}

h1::after {
  width: 0.08em;
  background-color: black;
  animation: typewriter var(--typewriter-speed)
      steps(var(--typewriter-characters)) 1s forwards,
    blink 750ms steps(var(--typewriter-characters)) infinite;
}

@keyframes typewriter {
  to {
    left: 100%;
  }
}

@keyframes blink {
  to {
    background: transparent;
  }
}
.subtitle {
  color: hsl(0 0% 0% / 0.7);
  font-size: 2rem;
  font-weight: 400;
  opacity: 0;
  transform: translateY(3rem);
  animation: fade-in-up 2s ease 4s forwards;
}

@keyframes fade-in-up {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
